.navbar {
    position: fixed;
    bottom: 40px;
    left: 50%;
    transform: translateX(-50%);
    width: auto;
    max-width: 700px;
    backdrop-filter: blur(10px);
    border: 1px solid rgba(159, 159, 159, 0);
    border-radius: 20px;
    color: white;
    padding: 10px 20px;
    display: flex;
    align-items: center;
    z-index: 1000;  
    height: auto;
}

.navbar:hover {
    background-color: rgba(0, 0, 0, 0.3);
    box-shadow: 0px 5px 5px rgba(0, 0, 0, 0.5);
    transition: background-color 0.4s, box-shadow 0.4s;
}

.navbar:not(:hover) {
    transition: background-color 0.4s, box-shadow 0.4s;
}

.navbar-links {
    list-style: none;
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    gap: 1rem;
    margin-left: 0;
    padding: 0px 20px;
}

.navbar-links a {
    flex: 1;
    padding: 15px 25px;
    border: 1px solid rgba(255, 255, 255, 0.5);
    border-radius: 10px;
    display: grid;
    justify-content: center;
    grid-template-columns: 1fr 0.2fr;
    gap: 10px;
    cursor: pointer;
    color: rgb(255, 255, 255);
    text-decoration: none;
    font-weight: bold;
    width: auto;
    text-align: center;
}

.navbar-links a:hover {
    background-color: rgba(0, 147, 233, 0.7);
    box-shadow: inset 0px 5px 5px rgba(0, 0, 0, 0.5);
    transition: background-color 0.4s, transform 0.4s, box-shadow 0.4s;
    color: white;
    text-decoration: none;
    transform: scale(1.1);
}

.navbar-links a:not(:hover) {
    transition: background-color 0.4s, transform 0.4s, box-shadow 0.4s;
}

.navbar-icon {
    height: 20px;
}
.navbar-links li {
    margin: 0;
}
  
